"use client";

import { motion } from "framer-motion";
import Image from "next/image";

export default function LeftSideBox() {
  return (
    <motion.div
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ duration: 1 }}
      className="relative w-1/2 h-full overflow-hidden flex items-center justify-center bg-[#f7f7f7]"
    >
      {/* Background image */}
      <motion.div
        initial={{ opacity: 0, scale: 1.05 }}
        animate={{ opacity: 1, scale: 1 }}
        transition={{ duration: 1.2, ease: "easeOut" }}
        className="absolute top-0 left-0 h-full w-[90%] overflow-hidden z-0 bg-[#f7f7f7]"
      >
        <Image
          src="/images/getstarted-leftside2.png"
          alt="Background"
          fill
          priority
          className="object-cover"
        />
      </motion.div>

      {/* Foreground images stacked vertically */}
      <div className="relative z-10 flex flex-col items-center justify-center space-y-12">
        <motion.div
          initial={{ opacity: 0, y: 30 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.6, duration: 0.8, ease: "easeOut" }}
          className="relative w-[33%] h-auto aspect-[1/1.2] -ml-32"
        >
          <Image
            src="/images/guy.png"
            alt="Guy"
            fill
            className="object-contain"
          />
        </motion.div>

        <motion.div
          initial={{ opacity: 0, y: 30 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.9, duration: 0.8, ease: "easeOut" }}
          className="relative w-[70%] h-auto aspect-[3/1] -ml-24"
        >
          <Image
            src="/images/earn.png"
            alt="Earn"
            fill
            className="object-contain"
          />
        </motion.div>
      </div>
    </motion.div>
  );
}
